.footer {
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0em;
    color: #545454;
    display: flex;
    align-items: center;
    padding: 48px 19px 36px;
}

@media screen and (min-width: 622px) {
    .footer {
        font-size: 18px;
        padding: 66px 0 60px;
    }
}